.navbar {
    background-color: #2d6a4f;
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .navbar-brand {
    font-size: 1.5rem;
    color: white;
    font-weight: bold;
  }
  
  .navbar-links {
    list-style: none;
    display: flex;
    gap: 1rem;
  }
  
  .navbar-links a {
    text-decoration: none;
    color: white;
    font-weight: 500;
  }
  
  .navbar-links a:hover {
    color: #d4f1f4;
  }
  